.service img{
	width: 100%;
}

.service-item{
	-webkit-transition: all .45s ease;
	   -moz-transition: all .45s ease;
	    -ms-transition: all .45s ease;
	     -o-transition: all .45s ease;
	        transition: all .45s ease;

	img{
		border-radius: 3px; 
	}

	&:hover{
		margin-top: -20px;
	}
}

.service-item-content{
	h3{
		position: relative;
		padding-bottom: 10px;
		font-weight: 600;

		&:before{
			position: absolute;
			content:"";
			left: 0px;
			bottom: 0px;
			width: 45px;
			height: 2px;
			background: $primary-color;
		}
	}
}

.service-item-2{
	.text-md{
		font-weight: 600;
	}

	img{
		box-shadow:0 20px 50px 0 rgba(46,65,90,.1);
		border-radius: 3px;
	}

	div{
		padding:10px 30px 0px 30px;
	}
}