
/*=== MEDIA QUERY ===*/

@include mobile-xs{

  .navbar-toggler{
    background: $light;
  }

  .navbar{
    background: $black;
  }
  
  .dropdown-menu{
    display: none;
    width: 100%;
    text-align: center;
  }

  .about-item-list li{
      width: 100%;
  }

  .text-md {
    font-size: 21px;
    line-height: 34px;
  }
  .about-video {
    margin-top: 0px;
  }

  .subscribe .form-control{
    width: 100%;
  }
  .subscribe .btn {
    width: 100%;
    margin-top: 10px;
  }
  .navbar-nav .nav-link {
    padding: 18px 0px;
  }
  .slider {
    padding: 163px 0 220px 0;
  }
  .slider h1 {
    font-size: 23px;
    line-height: 43px;
  }
}

