
a{
  color: $extra-color;
  -webkit-transition: all .35s ease;
     -moz-transition: all .35s ease;
      -ms-transition: all .35s ease;
       -o-transition: all .35s ease;
          transition: all .35s ease;
}

a:hover,a:focus{
  text-decoration: none;
  color: $primary-color;
}

.navbar-toggle .icon-bar {
  background: $primary-color;
}

input[type="email"],input[type="password"],input[type="text"],input[type="tel"]{
  box-shadow:none;
  height:45px;
  outline: none;
  font-size:14px;
  &:focus {
    box-shadow: none;
    border:1px solid $primary-color;
  }
}


.form-control {
  box-shadow: none;
  border-radius: 0;
  &:focus {
    box-shadow:none;
    border:1px solid $primary-color;
  }
}

//

.py-7{
  padding: 7rem 0px;
}

// Button Style

.btn{
  display: inline-block;
  font-size: 14px;
  padding: 13px 30px;
  text-transform: uppercase;
  border-radius:0;
  letter-spacing: 1px;
  font-weight: 700;
  @include transition (all, 0.2s, ease);
 
  &:hover {
    background: darken( $primary-color, 20% );
    color: $light;
  }
}

.btn-main {
  background: $primary-color;
  color: $light;
 
  &:hover {
    background: darken( $primary-color, 20% );
    color: $light;
  }
}

.btn:hover,.btn:focus{
  box-shadow: none !important;
}

.btn-solid-border {
  border:2px solid $primary-color;
  background:transparent;
  color:$light;
  &:hover {
    border:2px solid $primary-color;
    background:$primary-color;
  }
}


.btn-transparent {
  @extend .btn-main;
  background:transparent;
  padding:0;
  color:$primary-color;
  &:hover {
    background:transparent;
    color:$primary-color;
  }
}



.btn-round {
  border-radius:4px;
}
.btn-round-full {
  border-radius:50px;
}


.btn.active:focus, .btn:active:focus, .btn:focus {
  outline: 0;
}



.bg-gray {
  background:#f6f4f0 !important;
}


.bg-primary {
  background:$primary-color;
}
.bg-secondary {
  background:$secondary-color;
}
.bg-extra {
  background:$extra-color;
}


.bg-dark {
  background:#202122;
}

.letter-spacing-5{
  letter-spacing: 5px
}
.section {
  padding:100px 0;
}

.section-title {
  margin-bottom: 70px;

  span{
    letter-spacing: 5px;
    font-size: 13px;
  }
 
 .title{
  font-size: 40px;
  line-height: 50px;
  color: $extra-color;
 }
  p {
      color: #666;
      font-family:$secondary-font;
  }
}

// Page titile

.page-title{
  padding: 200px 0px 114px;
  @extend .overly;
  .block{
    h1{
      color:$light;
      text-transform: capitalize;
      position: relative;
      padding-bottom: 25px;

      &:before{
        position: absolute;
        content:"";
        left: 0px;
        width: 50px;
        height:4px;
        background: $primary-color;
        bottom: 0px;
      }
    }
    p{
      text-transform: uppercase;
      letter-spacing: 5px;
      margin-bottom: 10px;
    }
  }
} 
.breadcrumbs li{
  position: relative;
  padding: 0px 10px 0px 10px;
  color: $light;
  text-transform: uppercase;
  font-size: 13px;

    &:first-child:before {
      position: absolute;
      top: 0;
      bottom: auto;
      right: -8px;
      left: auto;
      content: "/";
    }

  }

   .breadcrumbs li.active{
      color: $primary-color;
    }



.page-wrapper {
  padding:70px 0;
}



.overly {
  position: relative;
  &:before{
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background:$extra-color;
    opacity: 0.92;
  }
}

