.navbar-nav{

  li{
    padding: 0px 10px;
  }
 .nav-link{
    font-family: $secondary-font;
    position: relative;
    font-weight: 600;
    letter-spacing: 0px;
    padding: 30px 0px;
    color: $light;
    font-size: 16px;
    letter-spacing: 1px;
    text-transform: uppercase;
    -webkit-transition: all .45s ease;
       -moz-transition: all .45s ease;
        -ms-transition: all .45s ease;
         -o-transition: all .45s ease;
            transition: all .45s ease;
    
    &:hover, &:focus{
      color: $primary-color;
    }

    &:hover:before{
        position: absolute;
        content: "";
        left: 8px;
        bottom: 0px;
        border-bottom: 1px solid $primary-color;
        width: 24px;
    }
  }

  .nav-link.active{
      &:before{
        position: absolute;
        content: "";
        left: 8px;
        bottom: 0px;
        border-bottom: 1px solid $primary-color;
        width: 24px;
      }
    }
}

#navbar{
  border-bottom: 1px solid rgba(255,255,255,.04);
  padding-bottom: 0px;
  .btn{
    padding: 12px 30px;
  }
}


.navbar-brand img{
    max-width: 220px;
}

.navbar-light{
  background: $light;
  width: 100%;
}

.dropdown{
  position: relative;
}
.dropdown-menu{
  visibility: hidden;
  filter: alpha(opacity=0);
  opacity: 0;
  width: 250px;
  padding: 10px;
  border-radius: 0px;
  border-top: 3px solid $primary-color;
  background: $black;
  box-shadow: 2px 2px 5px 1px rgba(0,0,0,0.05),-2px 0px 5px 1px rgba(0,0,0,0.05);
  webkit-transform: translateY(20px);
  -ms-transform: translateY(20px);
  transform: translateY(20px);
  transition: all 500ms ease;
  -moz-transition: all 500ms ease;
  -webkit-transition: all 500ms ease;
  -ms-transition: all 500ms ease;
  -o-transition: all 500ms ease;
  position: absolute !important;
  left: 0px;
  top: 100%;
  width: 240px;
  z-index: 100;
  display: block !important;
  margin: 0 auto;
}

.dropdown-toggle::after {
    display: none;
}

.dropdown:hover .dropdown-menu{
    visibility: visible;
    opacity: 1;
       webkit-transform: translateY(0px);
      -ms-transform: translateY(0px);
      transform: translateY(0px);
}
.dropdown-item{
    padding: 12px 23px;
    text-transform: uppercase;
    font-size: 13px;
    color: $light!important;
    letter-spacing: 1px;
     -webkit-transition: all .45s ease 0s;
       -moz-transition: all .45s ease 0s;
        -ms-transition: all .45s ease 0s;
         -o-transition: all .45s ease 0s;
            transition: all .45s ease 0s;
}

.dropdown-item:hover{
  background: $primary-color;
  color: $light;
}

ul.dropdown-menu li {
    padding-left: 0px!important;
}

.nav-top{
   position: fixed;
  top: 0;
  width: 100%;
 background-image: -moz-linear-gradient(rgb(46, 50, 56) 98px, rgba(0, 0, 0, 0) 98px);
 background-image: -o-linear-gradient(rgb(46, 50, 56) 98px, rgba(0, 0, 0, 0) 98px);
 background-image: -webkit-linear-gradient(rgb(46, 50, 56) 98px, rgba(0, 0, 0, 0) 98px);
 background-image: -ms-linear-gradient(top, #2e3238 98px, transparent 98px);
 background-image: linear-gradient(rgb(46, 50, 56) 98px, rgba(0, 0, 0, 0) 98px);
  -webkit-transition: .0s all ease;
  -o-transition: .0s all ease;
  transition: .0s all ease;
  -ms-transform: translateY(-100%);
  transform: translateY(-100%);
}
.nav-top.awake {
    -webkit-transition: .3s all ease;
    -o-transition: .3s all ease;
    transition: .3s all ease;
    -webkit-transform: translateY(0%);
    -ms-transform: translateY(0%);
    transform: translateY(0%);
}

.nav-top.sleep {
    -webkit-transition: .3s all ease;
    -o-transition: .3s all ease;
    transition: .3s all ease;
    -ms-transform: translateY(-80%);
    transform: translateY(-80%);
}

