.contact-form{
  .block{
      .form-group{
        padding-bottom: 15px;
        margin: 0px;
        .form-control{
          background: #F6F8FA;
          height: 60px;
          border: 1px solid #EEF2F6;
          box-shadow: none;
          width: 100%;
        }
      }
  }
  .form-group-2{
      margin-bottom: 13px;
      textarea{
        background: #F6F8FA;
        border: 1px solid #EEF2F6;
        box-shadow: none;
        width: 100%;
      }
    }
}



.google-map {
    border: 10px solid #EEF2F6;
}

.google-map #map {
    height: 350px;
}


