

// Fonts 

@import url('https://fonts.googleapis.com/css?family=Lato:400,700|Libre+Baskerville');

body,html{
   width: 100%;
}
body {
  line-height: 1.5;
  font-family: $secondary-font;
  -webkit-font-smoothing: antialiased;
  font-size: 16px;
}
h1,.h1,h2,.h2,h3,.h3,h4,.h4,h5,.h5,h6,.h6 {
  font-family: $secondary-font;
  font-weight:600;
}

h1,.h1 {
  font-size:40px;
   font-family: $primary-font; 
}
h2,.h2{
  font-size: 32px;
  line-height: 44px;
   font-family: $primary-font; 
}
h3,.h3{
  font-size: 22px;
  line-height: 34px;
   font-family: $primary-font; 
}
h4 ,.h4{
  font-size:20px;
  line-height: 30px;
   font-family: $primary-font; 
}

h5,.h5 {
  font-size:18px;
}
h6,.h6 {
  font-size:16px;
}


p {
  color:#7B7B7B;
}