.about-content{
	padding:0px 40px;
	border-radius: 3px;
	ul{
		margin: 30px 0px;
	}

	.feature{
		h4{
			font-family: $secondary-font;
			color: #000;
		}
		i{
			margin-right: 15px;
			color: $primary-color;
			margin-top: 7px;
			float: left;
		}
	}
}


// Video Block
// 


.hero-img{
	background: url("../images/bg/bg-3.jpg")no-repeat;
	background-size: cover;
	position: absolute;
	content:"";
	left: 0px;
	top:0px;
	width: 100%;
	@extend .overly-2;
}

.h70{
	height: 70%;
}

.about-video{
	margin-top: 70px;

	.video-img{
		position: relative;
		@extend .overly-3;
		img{
			border-radius: 10px
		}
	}

	a{
		position: absolute;
		content:"";
		left: 50%;
		top:50%;
		margin-left: -50px;
		margin-top: 50px;
		width: 120px;
		height: 120px;
		display: block;
		background: $light;
		border-radius: 100%;
		text-align: center;
		padding-top:35px;
		color:$primary-color;
		font-size: 30px;
	}
}


// ABout Home
// 


.about-item-list{
	li{
		width: 45%;
		float: left;
		margin-bottom: 15px;
		font-weight: 700;
		font-size: 18px;
	}
}


