.team-item{
	margin-bottom: 50px;

	.team-item-content{
		background: $secondary-color;
	}

	.team-socials{
		margin-top: -50px;
		text-align: center;
		opacity: 0;
		-webkit-transition: all .45s ease;
		   -moz-transition: all .45s ease;
		    -ms-transition: all .45s ease;
		     -o-transition: all .45s ease;
		        transition: all .45s ease;

		
		a{
			color: $light;
			width: 25px;
			height: 25px;
			text-align: center;
			background: $primary-color;
			display: inline-block;
		}
	}

	&:hover .team-socials{
		opacity: 1;
	}
}


.team-item.slick-slide{
	opacity: .5;
	transform: scale(.90);
	-webkit-transition: all .45s ease;
	   -moz-transition: all .45s ease;
	    -ms-transition: all .45s ease;
	     -o-transition: all .45s ease;
	        transition: all .45s ease;
}


.slick-current.slick-active.slick-center {
    opacity: 1;
    transform: scale(1);
}

.team-item-2{
	padding: 30px;
	background: $light;
	box-shadow: 0 2px 48px 0 rgba(0, 0, 0, 0.13);
	position: relative;

	.team-img{
		position: relative;
		z-index: 2;
		width: 180px;
		height: 180px;
		float: left;
		margin-right: 25px;
		bottom: 0px;
		margin-left: -80px;
		img{
			border-radius: 3px;
			-webkit-transition: all 0.3s ease 0s;
			-moz-transition: all 0.3s ease 0s;
			-o-transition: all 0.3s ease 0s;
			transition: all 0.3s ease 0s;
		}
	}

	.team-socials-2{
		margin-top: 25px;
		a{
			color: $extra-color;
		}
	}

	&:before{
		content: '';
		position: absolute;
		width: 5px;
		height: 85px;
		right: 0px;
		background: #DDDDDD;
		-webkit-transition: all 0.3s ease 0s;
		-moz-transition: all 0.3s ease 0s;
		-o-transition: all 0.3s ease 0s;
		transition: all 0.3s ease 0s;
	}

	&:hover:before{
		background: $extra-color;
	}

	&:hover .team-img img{
		margin-top: -18px;
	}

}


// Team -2 
// 


.team-block{
	h4{
		font-size: 18px;
	}

	.card-body{
		padding: 13px 30px;
		box-shadow: 10px 17px 38px 0 rgba(0,0,0,0.1);
	}
	.team-socials-block{
		position: absolute;
		bottom: 45px;
		right: 20px;
		border-radius: 25px;
		padding: 5px 17px;
	}
	.team-socials-block a{
		margin-right: 10px;

		i:hover{
			color: $primary-color;
		}
	}
}

i.ti-facebook{
	color: #3b5998;
}

i.ti-twitter{
	color: #55acee;
}
i.ti-pinterest{
	color: #bd081c;
}

i.ti-linkedin{
	color: #0077b5;
}
i.ti-google{
	color: #dc4e41;
}