/*=================================================================
  Latest Posts
==================================================================*/


.blog-item{
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.1);

 .blog-item-img {
   overflow: hidden;
 }
  .blog-item-img img{
    -webkit-transition: all .35s linear;
       -moz-transition: all .35s linear;
        -ms-transition: all .35s linear;
         -o-transition: all .35s linear;
            transition: all .35s linear;
            border-radius: 3px;
  }

  h4{
    color: $extra-color;
     -webkit-transition: all .35s linear;
       -moz-transition: all .35s linear;
        -ms-transition: all .35s linear;
         -o-transition: all .35s linear;
            transition: all .35s linear;
  }

  span{
    position: absolute;
    top: 15px;
    background: rgba(255, 255, 255, 0.9);
    padding: 3px 5px;
    font-size: 14px;
    right: 20px;
    border-radius: 3px;
  }

  .blog-item-content{
    padding: 35px;
    position: relative;

    &:before{
      content: '';
      position: absolute;
      width: 5px;
      height: 65px;
      left: 0px;
      background: #DDDDDD;
      -webkit-transition: all 0.3s ease 0s;
      -moz-transition: all 0.3s ease 0s;
      -o-transition: all 0.3s ease 0s;
      transition: all 0.3s ease 0s;
    }
    li{
      color: #666;
    }
  }

  &:hover .blog-item-img img{
    -webkit-transform: scale(1.1);
       -moz-transform: scale(1.1);
        -ms-transform: scale(1.1);
         -o-transform: scale(1.1);
            transform: scale(1.1);
  }

  &:hover h4{
    color: $primary-color;
  }

  &:hover .blog-item-content:before{
    background: $primary-color;
  }
}
.blog-meta{
  margin-top: 30px;
  font-size: 14px;
}



.blog-item-wrap{
 text-align: center;

  .blog-item-content-2{

    h3 a{
      font-weight: 700;
    }
    span{
      font-size: 14px;
    }

    p{
      font-size: 15px;
      line-height: 30px;
      color:$black;
    }
  }
}




.text-underline{
  text-decoration: underline;
}





// Single POst

.post {
  background: $light;
  margin-bottom: 40px;
  padding-bottom:20px;
  .post-media {
    &.post-thumb {
      img {
        width: 100%;
        height:auto;
      }
    }
    &.post-media-audio {
      iframe {
        width: 100%;
      }
    }
  }
  .post-title {
    margin-top: 10px;
    margin: 25px 0 0;
    text-transform:uppercase;
    a {
      color: $primary-color;
      &:hover {
        color: $primary-color;
      }
    }
  }
  .post-meta {
    font-size: 13px;
    margin-top: 10px;
    ul {
      li {
        display: inline-block;
        color:#909090;
        margin-right: 20px;
        font-size: 12px;
        letter-spacing: .5px;
        a {
          color:#909090;
          &:hover {
            color:$primary-color;
          }
        }
      }
    }
    .post-author {
      color:$black;
    }
  }
  .post-content {
    margin-top:20px;
    p {
      line-height:26px;
    }
    .btn-main {
      padding:10px 20px;
      margin:15px 0;
      font-size:12px;
    }
  }
}


.quote{
  line-height: 40px;
  padding: 60px 30px 60px 60px;
}

.font-size-14{
  font-size: 14px;
}


// Pagination
// 

.pagination{
  a{
    width: 40px;
    height: 40px;
    display: inline-block;
    border: 1px solid $border-color;
    font-weight: 700;
    padding-top: 6px;
    text-align: center;

    &:hover{
       background: $black;
      color: $light;
      border-color:$black;
    }
  }

  a.current{
    background: $black;
    color: $light;
    border: 1px solid $black;
  }
}