.slider{
  background: url("../images/bg/bg-2.jpg") no-repeat;
  background-size: cover;
  padding:290px 0 280px 0;
  position: relative;
  @extend .overly;
    text-align: center;
    h1{
      font-weight: 700;
      font-size: 65px;
      line-height: 90px;
      margin-bottom: 40px;
      text-transform:capitalize;
      color: $light;
    }
    span{
      letter-spacing: 10px;
    }
  }

 

  .btn-outline-light{
    &:hover{
      border-color: $light!important;
      color: $black;
      background: $light;
    }
}


.letter-spacing{
  letter-spacing: 1px;
}